import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
// import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { ColEditDelete, TableData } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { connect } from 'react-redux';
import { Card, Row, Col, message, Checkbox } from 'antd';
import to from 'await-to-js';
// import { getUserSetting } from 'services/userManagement';
import { getAllUser, deleteUser } from 'services/managementUser';

const dataUserType = {
  1: { key: 1, label: 'systemAdmin' },
  3: { key: 3, label: 'User' },
  2: { key: 2, label: 'Admin module' },
};

const mapStateToProps = state => ({
  userTypeId: state.app.userInfo.userTypeId,
  menuItem: state.app.userState.menuItems,
});
@connect(mapStateToProps)
class ManagementUser extends Component {
  state = {
    // dataTable: [],
    loading: false,
    reload: 0,
  };

  componentDidMount() {
    if (this.props.userTypeId !== 1) {
      window.location.href = `#/${this.props.menuItems[0].url}`;
    }
  }

  confirmDelete = async userId => {
    const [error] = await to(deleteUser(userId));
    if (error) return;
    message.success('Xóa thành công');
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  cancel = () => {};

  renderTable = () => {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Tên người dùng',
        dataIndex: 'userName',
        key: 'userName',
      },
      {
        title: 'Email',
        dataIndex: 'email',
        key: 'email',
      },
      {
        title: 'Phân quyền',
        dataIndex: 'userTypeId',
        key: 'userTypeId',
        render: (text, record) => (
          <Fragment>
            <div> {dataUserType[record.userTypeId].label}</div>
          </Fragment>
        ),
      },
      {
        title: 'Khóa',
        dataIndex: 'disabled',
        key: 'disabled',
        render: (text, record) => (
          <Fragment>
            <Checkbox checked={record.disabled} />
          </Fragment>
        ),
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            <ColEditDelete
              linkEdit={{
                to: {
                  pathname: '/quan-ly-nguoi-dung/nhap-du-lieu',
                  search: `?id=${record.id}`,
                },
              }}
              onConfirmDelete={() => this.confirmDelete(record.id)}
            />
          </Fragment>
        ),
      },
    ];
    return (
      <TableData
        loading={this.state.loading}
        paramSearch={{}}
        service={getAllUser}
        columns={columns}
        reload={reload}
        rowKey="id"
      />
    );
  };

  render() {
    return (
      <Card title="">
        <Row>
          <Col span={24}>
            <Link to="/quan-ly-nguoi-dung/nhap-du-lieu">
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>{this.renderTable()}</Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ManagementUser />
  </Page>
);



